body{
  background-color: #F0F0F0;
}

.container{
    margin-top: 50px;
    max-width: 840px;
    width: 100%;
    height: 751px;
    margin-left: auto;
    margin-right: auto;
    background-color: #FFFFFF;
    border-top-left-radius: 24px;
    border-top-right-radius: 24px;
    border-bottom-left-radius: 24px;
    border-bottom-right-radius: 200px;
    display: flex;
    flex-direction: column;

}
.labelText{
  font-family: "Poppins", sans-serif;
  font-weight: bold;
  font-size: 14px;
  color: #716F6F;
}
.labelTextError{
  color: #FF5959;
}
.inputText{
  font-family: "Poppins", sans-serif;
  font-weight: bold;
  width: 136px;
  height: 72px;
  font-size: 32px;
  color: #151515;
  margin-top: 8px;
  margin-right: 32px;
  border-radius: 8px;
  border-color: #DCDCDC;
  border: solid;
  border-width: 1px;
  padding-left: 24px;
}
.inputTextError{
  border-color: #FF5959;
}

.inputText:focus{
  border-color: #854DFF;
  outline-color: #854DFF;
  border-width: 1px;
}

.inputs-wrapper{
  display: flex;
  height: 130px;
  width: 728px;
  flex-direction: row;
  justify-content: center;
  justify-items: left;
  align-items: center;
  padding-top: 56px;
}

.inputWrapper{
  display: flex;
  flex-direction: column;
  height: 130px;
}
.h1-main{
  font-family: "Poppins", sans-serif;
  font-weight: bold;
  font-style: normal;
  font-size: 104px;
  margin: 0;
  height: 114px;
}

.daysWrapper{
  margin-left: 56px;
  height: 342px;
}

.days{
  color: #854DFF;
  font-family: "Poppins", sans-serif;
  font-weight: bold;
  font-style: normal;
  font-size: 104px;
}

.btnWrapper{
  display: flex;
  flex-direction: row;
  justify-content: center;
  justify-items: center;
  align-items: center;
}

.btn{
  width: 96px;
  height: 96px;
  border-radius: 100px;
  background-color: #854DFF;
  border-width: 0;
  background-image: url('/src/image/Frame\ 29.svg');
  background-repeat: no-repeat;
  background-position: center;
}

.btn:hover{
  background-color: #151515;
}

.btn:disabled{
  opacity: 30%;
}

.line{
  max-width: 632px;
  width: 100%;
  height: 1px;
  background-color: #DCDCDC;
}

.error{
  font-family: "Poppins", sans-serif;
  font-weight: italic;
  font-size: 14px;
  color: #FF5959;
  margin-top: 8px;

  

}

@media (width <=400px){

  .container{
    max-width: 400px;
    width: 100%;
    height: 550px;
    justify-content: center;
    justify-items: center;
    align-items: center;
  }

  .inputs-wrapper{
    padding: 0;
    width: 295px;
    justify-content: center;
    justify-items: center;
    align-items: center;
    padding-top: 48px;
    margin-bottom: 32px;
  }

  .labelText{
    font-size: 12px;
  }

  .inputText{
    font-size: 20px;
    width: 71px;
    height: 76px;
    margin-right: 16px;
    padding-left: 16px;  
  }

  .days{
    font-size: 48px;
  }

  .h1-main{
    font-size: 48px;
    height: 62px;
  }

  .daysWrapper{
    max-width: 295%;
    width: 100%;
    height: 186px;
    margin-left: 24px;
    margin-bottom: 48px;
  }

  .line{
    position: absolute;
    max-width: 295px;
    width: 100%;

  }

  .btn{
    z-index: 5;
    width: 64px;
    height: 64px;
  }

  .btnWrapper{
    margin-bottom: 32px;
  }

  .error{
    font-size: 8px;
    margin-right: 0;
    margin-top: 6px;
  }
}